body {
  background: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.card {
  text-align: center;
  background: #fff;
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

.card h1 {
  color: #3498db;
  margin-bottom: 1rem;
}

.card button {
  color: #fff;
  cursor: pointer;
  background: #3498db;
  border: none;
  border-radius: 4px;
  padding: .6rem 1.2rem;
  transition: background-color .3s;
}

.card button:hover {
  background-color: #217dbb;
}

.card.active h1 {
  color: #e74c3c;
}

.card.active button {
  background: #e74c3c;
}
